import commonStore from '@/store/common';

export function verify_urami() {
  if (commonStore.verification === false) {
    window.location.replace('/');
  }
  return null;
}

export function scrollToTopAndRoute(path: string) {
  window.scrollTo({
    top: 0,
    //这个属性兼容性有问题 详见firefox
    // behavior: 'smooth',
  });
  setTimeout(() => {
    commonStore.history.push(path);
  }, 0);
}
