import React, { Component } from 'react';
import { Route, Router, Switch } from 'react-router';
import MessageBoardPage from '@/pages/message_board';
import TopBar from '@/components/top_bar';
import HomePage from '@/pages/homepage';
import ArticleList from '@/pages/article_list';
import ArticlePage from '@/pages/article_page';
import { observer } from 'mobx-react';
import commonStore from '@/store/common';
import Avatar from '@/components/avatar';
import { scrollToTopAndRoute } from './utils/func';

interface IProps {}

interface IState {}

@observer
class Routers extends Component<IProps, IState> {
  render() {
    return (
      <Router history={commonStore.history}>
        <TopBar>
          <TopBar.Item onClick={() => scrollToTopAndRoute('/')}>
            <strong>上海喫茶館</strong>
          </TopBar.Item>
          <TopBar.Item onClick={() => scrollToTopAndRoute('/article_list')}>文章</TopBar.Item>
          <TopBar.Item onClick={() => scrollToTopAndRoute('/message_board')}>留言板</TopBar.Item>
          {commonStore.login_status ? (
            <TopBar.Item position="right" onClick={commonStore.github_logout}>
              <Avatar imgSrc={commonStore.github_avatar}></Avatar>登出
            </TopBar.Item>
          ) : (
            <TopBar.Item position="right" onClick={commonStore.github_login}>
              <Avatar />
              登入
            </TopBar.Item>
          )}
        </TopBar>

        <Switch>
          <Route path="/message_board" component={MessageBoardPage} />
          <Route path="/article_list" component={ArticleList} />
          <Route path="/article/*" component={ArticlePage} />
          <Route path="/" component={HomePage} />
        </Switch>
      </Router>
    );
  }
}
export default Routers;
