import React, { Component } from 'react';
import XXX from './lycoris.webp';
import flame_gif from './flame.webp';
import commonStore from '@/store/common';
import './index.css';
import flame_mp3 from './flame.mp3';
import jigoku_mp3 from './jigoku_refine.mp3';
import button_mp3 from './button.ogg';
// const flame_mp3 = require('./flame.mp3');
// const jigoku_mp3 = require('./jigoku_refine.mp3');
// const button_mp3 = require('./button.ogg');

interface IProps {}

interface IState {
  playFlameEnd: boolean;
  flameCanPlay: boolean;
  uramiValue: string;
  canJump: boolean;
}

class HomePage extends Component<IProps, IState> {
  private flameAudio = new Audio(flame_mp3);
  private jigokuAudio = new Audio(jigoku_mp3);
  private buttonAudio = new Audio(button_mp3);
  private flameImg = new Image();

  static getDerivedStateFromProps = (_nextProps: IProps, _prevState: IState) => {
    return null;
  };

  state = {
    uramiValue: '',
    playFlameEnd: false,
    flameCanPlay: false,
    canJump: false,
  };

  componentDidMount = async () => {
    this.jigokuAudio.volume = 1;
    this.flameAudio.volume = 1;
    this.jigokuAudio.loop = true;
    this.flameImg.src = flame_gif;
    this.flameAudio.addEventListener('ended', this.handleFlameAudioEnded);
  };

  componentWillUnmount = () => {
    this.flameAudio.removeEventListener('ended', this.handleFlameAudioEnded);
  };

  handleFlameAudioEnded = () => {
    this.setState(
      {
        playFlameEnd: true,
      },
      () => {
        this.jigokuAudio.play();
      }
    );
  };

  handleInitClick = () => {
    this.flameAudio.play();
    this.flameAudio.autoplay = true;
    this.setState({ flameCanPlay: true });
  };

  adjustMidnight = () => {
    const date = new Date();
    let hour = date.getHours();
    let minute = date.getMinutes();
    if ((hour === 23 && minute === 59) || (hour === 0 && minute <= 1)) {
      return true;
    } else {
      return false;
    }
  };

  handleSendMessageClick = async () => {
    let correntTime = Math.floor(Date.now() / 1000);
    if (!this.state.uramiValue) {
      //未填充
    } else if (
      (Number(this.state.uramiValue) > correntTime - 60 && Number(this.state.uramiValue) < correntTime + 60) ||
      this.adjustMidnight()
    ) {
      //
      commonStore.set_urami();
      //
      this.setState({
        canJump: true,
      });
      //处理safari不能播放ogg
      //必须await play方法
      try {
        await this.buttonAudio.play();
      } catch (e) {
        alert('SAFARI SUCKS! DO NOT USE SAFARI!\n' + e);
      }
      let v = 1;
      const that = this;
      let k = setInterval(function () {
        v -= 0.05;
        // console.log('v1' + v);
        if (v > 0) {
          that.jigokuAudio.volume = v;
        } else {
          that.jigokuAudio.pause();
          clearInterval(k);
          //漸隱，跳轉
          commonStore.history.push('/article_list');
        }
      }, 200);
    } else {
      //填充了错误内容
    }
  };

  handleUramiChange = (e: any) => {
    this.setState({
      uramiValue: e.target.value,
    });
  };

  render() {
    return (
      <div
        style={{
          display: 'flex',
          position: 'fixed',
          top: 0,
          left: 0,
          background: 'black',
          width: '100%',
          height: '100vh',
          color: 'white',
          paddingTop: '10vh',
          zIndex: 100,
          // overflowY: 'hidden',
          flexDirection: 'column',
          alignItems: 'center',
          cursor: 'auto',
        }}
      >
        <div
          style={{
            opacity: `${this.state.playFlameEnd ? (this.state.canJump ? '0' : '1') : '0'} `,
            transition: 'all 4.5s',
            width: '100%',
          }}
        >
          <div
            style={{ position: 'relative', zIndex: 1, flexDirection: 'column', alignItems: 'center', display: 'flex' }}
          >
            <div
              style={{ fontSize: `calc(10px + 2vmin)`, textShadow: 'rgb(255,255,255) 0 0 1.3px', color: 'transparent' }}
            >
              あ な た の 怨 み 、 晴 ら し ま す 。
            </div>
            <input
              style={{ marginTop: 15, width: '80%', maxWidth: 200 }}
              value={this.state.uramiValue}
              autoComplete="off"
              onChange={this.handleUramiChange}
            ></input>
            <button onClick={this.handleSendMessageClick} style={{ marginTop: 25 }}>
              送信
            </button>
          </div>
          <div
            style={{
              display: 'flex',
              backgroundImage: `url(${XXX})`,
              backgroundPosition: 'center center',
              backgroundRepeat: 'no-repeat',
              backgroundSize: 'cover',
              width: '100%',
              height: '80vh',
              bottom: 0,
              position: 'fixed',
              boxShadow: '0 0 50px 30px #000 inset',
            }}
          ></div>
        </div>

        {this.state.flameCanPlay ? (
          this.state.playFlameEnd ? null : (
            <img src={this.flameImg.src} alt="flame" style={{ position: 'fixed', top: '8vh' }} />
          )
        ) : (
          <button onClick={this.handleInitClick} style={{ position: 'fixed', top: '20vh' }}>
            地獄通信
          </button>
        )}
      </div>
    );
  }
}

export default HomePage;
