import Router from './Router';
import './App.css';
import React, { Component } from 'react';
import queryString from 'query-string';
import { observer } from 'mobx-react';
import commonStore from '@/store/common';
import WholePageLoading from '@/components/whole_page_loading';
import Footer from '@/components/footer';
interface IProps {}

interface IState {}

@observer
class App extends Component<IProps, IState> {
  state = {};

  static getDerivedStateFromProps = (_nextProps: IProps, _prevState: IState) => {
    //待优化
    let github_code = queryString.parse(window.location.search).code;
    // //若已登录过一次,又刷新页面的场景. localStorage里面有数据,但是mobx状态没初始化
    // if (localStorage.getItem('github_access_token') && !commonStore.login_status) {
    //   commonStore.init_user_info();
    // } else if (github_code && commonStore.login_status === false) {
    //   //登陆的第一次重定向刚回来
    //   commonStore.whole_page_loading = true;
    //   commonStore.github_access_token(github_code.toString());
    // }

    //第一次登录重定向回来的场景
    //localStorage没有东西,并且url里有github_code,代表需要继续登录的流程
    if (!localStorage.getItem('github_access_token') && github_code) {
      commonStore.set_urami();
      commonStore.github_access_token(github_code.toString());
    }
    //若已登录过一次,又刷新页面的场景. localStorage里面有数据,但是mobx状态没初始化
    if (localStorage.getItem('github_access_token') && !commonStore.login_status) {
      commonStore.init_user_info();
    }
    return null;
  };

  render() {
    return (
      // {/* 需要预渲染的css不要写在css里面 会被bundle到外联css里,预渲染出来的没有样式(必须要等到css回来才有样式) */}
      // {/* 除非配置webpack把全部css内联到html */}
      <>
        {commonStore.whole_page_loading ? <WholePageLoading /> : null}
        <Router />
        <Footer />
      </>
    );
  }
}

export default App;
