import React, { Component } from 'react';
import './index.css';
import axios from 'axios';
import { ArticleSummary } from '@/components/article_card';
import CommentModule from '@/components/comment_module';
import { message } from '@/components/toast';
import { divder, my_client_id, my_client_secret } from '@/utils/const';
import { verify_urami } from '@/utils/func';

interface IProps {}

interface IState {
  article_detail: ArticleSummary;
  loading: boolean;
}

class ArticlePage extends Component<any, IState> {
  private issue_number: undefined | string;
  state = {
    article_detail: {} as ArticleSummary,
    loading: false,
  };

  static getDerivedStateFromProps = (_nextProps: IProps, _prevState: IState) => {
    verify_urami();
    return null;
  };
  //TODO
  // URL格式类似:  xxxxx/article/文章标题/13(issue号码)
  // 在新页面打开 刷新时重新拉取这个issue的数据。当滚轮滚到评论位置时再加载评论

  componentDidMount = async () => {
    this.setState({
      loading: true,
    });
    let path_array = window.location.toString().split('/');
    this.issue_number = path_array[path_array.length - 2];
    //拿issue_number请求issue详情
    //Deprecated 加上client_id与client_secret可提升接口请求数量限制
    const headers1 = {
      Accept: `application/vnd.github.squirrel-girl-preview, application/vnd.github.html+json`,
      Authorization: `${my_client_id}:${my_client_secret}`,
    };
    try {
      let article_promise = axios.get(
        `https://api.github.com/repos/wallena3/Personal-Website-Articles/issues/${this.issue_number}`,
        { headers: headers1 }
      );

      const [article_detail] = await Promise.all([article_promise]);

      this.setState({
        article_detail: article_detail.data,
        loading: false,
      });
    } catch (e) {
      //TODO 重定向到404页面
      message('没有找到页面哦');
      this.setState({
        loading: false,
      });
    }
  };

  render() {
    return (
      <div style={{ minHeight: '100vh', display: 'flex', overflow: 'hidden' }}>
        <div className="left_slide">
          <span role="img" aria-label="palyer">
            ▶️
          </span>
        </div>

        <div
          style={{
            flex: 'auto',
            display: 'flex',
            alignItems: 'center',
            flexDirection: 'column',
            wordBreak: 'break-all',
          }}
        >
          {this.state.loading ? (
            '加载中'
          ) : (
            <>
              <h2>{this.state.article_detail.title}</h2>
              {this.state.article_detail.milestone?.title}
              {this.state.article_detail.milestone?.description}
              <div
                className="markdown-body"
                dangerouslySetInnerHTML={{ __html: this.state.article_detail?.body_html?.split(divder)?.[1] }}
                style={{
                  wordBreak: 'break-all',
                  whiteSpace: 'normal',
                  fontSize: 15,
                  display: 'flex',
                  flexDirection: 'column',
                  width: 'calc(200px + 20vw)',
                }}
              />
              <div
                style={{
                  wordBreak: 'break-all',
                  whiteSpace: 'normal',
                  display: 'flex',
                  flexDirection: 'column',
                  width: 'calc(200px + 20vw)',
                }}
              >
                {' '}
                <CommentModule issue_number={this.issue_number}></CommentModule>
              </div>
            </>
          )}
        </div>

        <div className="right_slide">
          <span role="img" aria-label="palyer">
            ▶️
          </span>
        </div>
      </div>
    );
  }
}

export default ArticlePage;
