import React, { Component } from 'react';
import Avatar from '@/components/avatar';
import TabeiNai from '@/assets/tabeinai.jpg';

import commonStore from '@/store/common';
import { message } from '../toast';
import axios from 'axios';
import { cors_server, my_client_id, my_client_secret } from '@/utils/const';

export interface CommentDetail {
  user: {
    avatar_url: string;
    login: string;
    html_url: string; //github主页地址
  };
  updated_at: string;
  body_html: string;
}

interface IProps {
  issue_number: string | undefined;
  style?: React.CSSProperties;
}

interface IState {
  comment: string;
  comment_list: CommentDetail[];
}

class CommentModule extends Component<IProps, IState> {
  state = {
    comment: '',
    comment_list: [] as CommentDetail[],
  };

  componentDidMount = async () => {
    this.fetchData();
  };

  fetchData = async () => {
    const headers1 = {
      Accept: `application/vnd.github.squirrel-girl-preview, application/vnd.github.html+json`,
      Authorization: `${my_client_id}:${my_client_secret}`,
    };
    if (this.props.issue_number) {
      let comment = await axios.get(
        `https://api.github.com/repos/wallena3/Personal-Website-Articles/issues/${
          this.props.issue_number
        }/comments?rnd=${Math.random()}`,
        { headers: headers1 }
      );
      this.setState({
        comment_list: comment.data,
      });
    }
  };

  handleCommentChange = (e: any) => {
    // setTimeout(() => {
    this.setState({ comment: e.target.value });
    // }, 500);
  };

  handleReplyClick = (beAtedUser: string) => {
    //点击reply后,跳转页面到留言板位置
    this.setState({ comment: beAtedUser });
    // 找到锚点
    let anchorElement = document.getElementById('comment_textarea');
    // 如果对应id的锚点存在，就跳转到锚点
    if (anchorElement) {
      anchorElement.scrollIntoView();
    }
  };

  handleCommentCreate = async () => {
    try {
      if (this.state.comment) {
        //https://docs.github.com/en/rest/reference/issues#create-an-issue-comment
        await axios.post(
          `${cors_server}/https://api.github.com/repos/wallena3/Personal-Website-Articles/issues/${this.props.issue_number}/comments`,
          {
            body: this.state.comment,
          },
          {
            headers: { Authorization: `token ${commonStore.access_token}` },
          }
        );
        message('发送成功');
        this.fetchData();
        this.setState({
          comment: '',
        });
      } else {
        message('还没有输入内容哦');
      }
    } catch (e) {
      message('没发出去呢？？重新登录再试试吧');
    }
  };

  renderCommentList = () => {
    return this.state.comment_list.map((comment, index) => {
      return (
        <div style={{ display: 'flex', marginBottom: 10, marginTop: 10 }} key={index}>
          <div>
            <Avatar imgSrc={comment.user.avatar_url}></Avatar>
          </div>
          <div style={{ display: 'flex', flexDirection: 'column' }}>
            <div>
              <strong style={{}}>
                <a href={comment.user.html_url} target="_blank" rel="noopener noreferrer">
                  {comment.user.login}
                </a>
              </strong>
              {'   '}
              {comment.updated_at}
            </div>
            <div
              dangerouslySetInnerHTML={{ __html: comment.body_html }}
              style={{
                wordBreak: 'break-all',
              }}
            ></div>
            <div>
              <button onClick={() => this.handleReplyClick(`@${comment.user.login} `)} style={{}}>
                回复
              </button>
            </div>
          </div>
        </div>
      );
    });
  };

  render() {
    return (
      <div style={{ ...this.props.style, marginTop: 20, marginBottom: 20 }}>
        <h1>留言板</h1>
        <hr />
        <textarea
          style={{
            width: '100%',
            height: '40vh',
            backgroundImage: `url(${TabeiNai})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'right',
            backgroundSize: 'contain',
            resize: 'none',
          }}
          placeholder={'支持markdown语法'}
          onChange={this.handleCommentChange}
          value={this.state.comment}
          id="comment_textarea"
        ></textarea>
        {commonStore.login_status ? (
          <button onClick={this.handleCommentCreate}>添加评论</button>
        ) : (
          <div>留言请登录哦</div>
        )}
        <div> {this.renderCommentList()}</div>
      </div>
    );
  }
}

export default CommentModule;
