import React, { Component } from 'react';
import './index.css';
import axios from 'axios';
// import commonStore from '@/store/common';
import Akari from '@/assets/akari.jpg';
import { cors_server, divder } from '@/utils/const';
import { scrollToTopAndRoute } from '@/utils/func';
export interface ArticleSummary {
  title: string;
  body_html: string;
  number: number; //issue号码
  created_at: string;
  updated_at: string;
  milestone?: {
    title: string; //描述
    description: string; //存放文章头图
  };
  labels: {
    name: string; //标签
  }[];
  comments: number; //评论条数
}

interface IState {
  url: string;
}

interface IProps {
  ArticleSummary?: ArticleSummary;
  loading?: boolean;
}

class ArticleCard extends Component<IProps, IState> {
  state = {
    url: '',
  };

  componentDidMount = async () => {
    const res = await axios.get(`${cors_server}/http://ssr0.cn:8000/ACG`, {
      responseType: 'blob',
    });
    // console.log(res);
    this.setState({
      url: window.URL.createObjectURL(res.data),
    });
  };

  render() {
    return (
      <div
        className="article_card"
        style={{
          display: 'flex',
          flexDirection: 'column',
          // alignItems: 'center',
          marginTop: 20,
          marginBottom: 10,
          fontSize: `calc(8px + 1vmin)`,
          color: '#504e4e',
          boxShadow: '0 1px 6px 0 rgba(0,0,0,.12),0 1px 6px 0 rgba(0,0,0,.12)',
          borderRadius: 2,
          backgroundColor: 'white',
          padding: 15,
          height: this.props.loading ? '70vh' : 'auto',
        }}
      >
        <h3 style={{ fontSize: 24, padding: '0 5px 5px', marginTop: 5, marginBottom: 10, wordBreak: 'break-all' }}>
          {this.props.loading ? (
            'loading...'
          ) : (
            // <a
            //   href={`/article/${this.props?.ArticleSummary?.title}/${this.props?.ArticleSummary?.number}/`}
            //   style={{ textDecoration: 'none', userSelect: 'none', outline: 0, color: '#504e4e' }}
            // >
            //   {this.props?.ArticleSummary?.title}
            // </a>
            <div
              onClick={() => {
                scrollToTopAndRoute(
                  `/article/${this.props?.ArticleSummary?.title}/${this.props?.ArticleSummary?.number}/`
                );
              }}
            >
              {this.props?.ArticleSummary?.title}
            </div>
          )}
        </h3>
        <div className={'post_meta'} style={{}}>
          {`#${this.props?.ArticleSummary?.created_at?.split('T')?.[0] || '宇宙未知时间'}  /  分类: ${
            this.props?.ArticleSummary?.labels[0]?.name || '暂无'
          }  /  评论: ${this.props.ArticleSummary?.comments || 0}条`}
        </div>

        <div
          className={'post_pre'}
          style={{
            marginBottom: 10,
            wordBreak: 'break-all',
            whiteSpace: 'normal',
            width: 'calc(100px + 50vw)',
          }}
          dangerouslySetInnerHTML={{ __html: this.props.ArticleSummary?.body_html?.split(divder)?.[0] || '' }}
        ></div>
        {/* 文章头图 */}
        <img
          style={{
            // width: '100%',
            // height: `calc(60px + 30vmin)`,
            // objectFit: 'cover',
            borderRadius: 2,
            boxShadow: `0 1px 6px 0 rgba(0,0,0,.12),0 1px 6px 0 rgba(0,0,0,.12)`,
            display: 'block',
            maxWidth: '100%',
            marginLeft: 'auto',
            marginRight: 'auto',
          }}
          // src={this.props.body_html.split('">')[0].substring(54)}
          src={this.state.url}
          onError={(e) => {
            e.currentTarget.src = Akari;
          }}
          // alt={`${this.props.title}`}
          alt={` `}
        />
      </div>
    );
  }
}

export default ArticleCard;
