import React, { Component } from 'react';
import Avatar from '../avatar';
import './index.css';
interface IProps {
  avatar_url?: string;
  name: string;
  desc: string;
  home_page: string;
}

class FriendLink extends Component<IProps, any> {
  render() {
    return (
      <div
        className="friend_link"
        onClick={() => {
          window.open(this.props.home_page, '_blank');
        }}
        style={{
          transition: 'all 0.4s',
          width: 260,
          height: 80,
          overflowY: 'hidden',
          border: 1,
          backgroundColor: 'snow',
          marginTop: 20,
          marginLeft: 10,
          //   borderColor: 'rgba(255, 255, 255, 0.2)',
          //   borderStyle: 'solid',
          //border计入div总宽高
          boxSizing: 'border-box',
          boxShadow: '6px 6px 20px rgba(0,0,0,.3)',
          display: 'flex',
        }}
      >
        {/* 头像 */}
        <div style={{ width: '40%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          <Avatar imgSrc={this.props?.avatar_url} size="large"></Avatar>
        </div>
        <div style={{ width: '60%', marginRight: 5, marginBottom: 5, marginTop: 8 }}>
          <div style={{ fontSize: 14 }}>{this.props.name}</div>
          <div style={{ width: '100%', borderBottom: '1px solid rgba(0,0,0,.1)', marginTop: 2, marginBottom: 4 }} />
          <div style={{ fontSize: 12, color: 'gray', whiteSpace: 'pre-wrap' }}>{this.props.desc}</div>
        </div>
      </div>
    );
  }
}

export default FriendLink;
