import React, { Component } from 'react';
import './index.css';
import Akari from '@/assets/akari.jpg';
interface IProps {
  imgSrc: string;
  size?: 'small' | 'normal' | 'large' | undefined;
}
interface IState {}

class Avatar extends Component<IProps, IState> {
  state = {};
  componentDidMount = () => {};

  static defaultProps: IProps = {
    imgSrc: '',
    size: undefined,
  };
  render() {
    return (
      <div
        className={
          this.props.size === 'small' || !this.props.size
            ? 'bgImg'
            : this.props.size === 'normal'
            ? 'bgImgNormal'
            : 'bgImgLarge'
        }
        style={{ backgroundImage: `url(${this.props.imgSrc}), url(${Akari})` }}
      ></div>
    );
  }
}

export default Avatar;
