import React, { Component } from 'react';
import axios from 'axios';
import './index.css';
import ArticleCard, { ArticleSummary } from '@/components/article_card';
import { message_board_number, my_client_id, my_client_secret } from '@/utils/const';
import RightSlider from '@/components/right_slider';
import commonStore from '@/store/common';
import { verify_urami } from '@/utils/func';

interface IProps {}

interface IState {
  articleList: ArticleSummary[];
  loading: boolean;
  totalCount: number;
}

const PAGECOUNT = 10;

class HomePage extends Component<IProps, IState> {
  state = {
    articleList: [] as ArticleSummary[],
    loading: false,
    totalCount: 0,
  };

  static getDerivedStateFromProps = (_nextProps: IProps, _prevState: IState) => {
    verify_urami();
    return null;
  };

  componentDidMount = async () => {
    this.setState({
      loading: true,
    });
    //https://developer.github.com/v3/issues/#list-repository-issues
    //加入头部，让api直接返回html模式而不是markdown的body
    const headers1 = {
      Accept: `application/vnd.github.html+json`,
      Authorization: `${my_client_id}:${my_client_secret}`,
    };
    //这个API没支持返回totalCount 一次请求分页数据，一次请求总数。
    let totalCountPromise = axios.get('https://api.github.com/repos/wallena3/Personal-Website-Articles/issues', {
      headers: headers1,
    });
    let singlePagePromise = axios.get(
      `https://api.github.com/repos/wallena3/Personal-Website-Articles/issues?page=0&per_page=${PAGECOUNT}`,
      {
        headers: headers1,
      }
    );
    let [totalCountRes, singlePageRes] = await Promise.all([totalCountPromise, singlePagePromise]);

    this.setState({
      totalCount: totalCountRes.data.length,
      articleList: singlePageRes.data,
      loading: false,
    });
  };

  render() {
    return (
      <div style={{ minHeight: '100vh', display: 'flex' }}>
        <div
          className="article_list"
          style={{
            flex: 'auto',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          {this.state.loading ? (
            <ArticleCard {...{ loading: true }} key={0} />
          ) : (
            this.state.articleList.map((article, index) => {
              //去除留言板
              if (article.number !== message_board_number) {
                return <ArticleCard {...{ ArticleSummary: article }} key={index} />;
              } else {
                return null;
              }
            })
          )}
        </div>
        <div className="right_slider" style={{ marginTop: 20 }}>
          {' '}
          <RightSlider></RightSlider>
        </div>
      </div>
    );
  }
}

export default HomePage;

