import React, { Component } from 'react';
import './menu_item.css';
interface IProps {
  position?: 'left' | 'right';
  onClick?: React.MouseEventHandler<HTMLElement>;
}

interface IState {}

//TODO: 增加一些通用的顶栏Item样式 比如hover变亮；左右padding；
class TopBarItem extends Component<IProps, IState> {
  static defaultProps = {
    position: 'left',
  };

  render() {
    const { onClick } = this.props;
    return (
      <div
        className="menu_item"
        onClick={onClick}
        style={{
          display: 'flex',
          alignItems: 'center',
          marginLeft: 8,
          userSelect: 'none',
          outline: 0,
          textDecoration: 'none',
          whiteSpace: 'nowrap',
          minWidth: 10,
          overflow: 'scroll',
          scrollbarWidth: 'none', //firefox 隐藏滚动条
          // WebkitOverflowScrolling: 'unset',
          msOverflowStyle: 'none', //IE隐藏滚动条
        }}
      >
        {this.props.children}{' '}
      </div>
    );
  }
}

export default TopBarItem;
