import React, { Component } from 'react';
import TopBarItem from './menu_item';
import './index.css';
//too big remove
import bilibili_autumn from '@/assets/bilibili_autumn.png';
import TopFix from '@/assets/topfixed_bg.png';
interface IProps {}

interface IState {
  isMobile: boolean;
  isFirstScreen: boolean;
  TopBarBackgroundHeight: number;
}

class TopBar extends Component<IProps, IState> {
  state = { isMobile: false, isFirstScreen: true, TopBarBackgroundHeight: 0 };
  static Item = TopBarItem;
  componentDidMount = () => {
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('scroll', this.handlePageScroll);
    let div = document.getElementById('TopBarBackground');
    this.setState({
      TopBarBackgroundHeight: Number(div?.offsetHeight) || 0,
    });
  };

  //监听屏幕宽度，使用不同样式
  handleResize = () => {
    //避免频繁触发
    // setTimeout(() => {
    //   console.log(document.body.clientWidth);
    // }, 200);
  };

  handlePageScroll = () => {
    if (window.scrollY >= this.state.TopBarBackgroundHeight - 20) {
      this.setState({
        isFirstScreen: false,
      });
    } else {
      this.setState({
        isFirstScreen: true,
      });
    }
  };

  componentWillUnmount = () => {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('scroll', this.handlePageScroll);
  };

  renderTopBarBottom = () => {
    return (
      <div
        style={{
          backgroundImage: `url(${TopFix})`,
          backgroundRepeat: 'repeat-x',
          backgroundPosition: '0 bottom',
          height: 3,
          width: '100%',
          boxShadow: '0 2px 8px rgba(0,0,0,.25),0 2px 8px rgba(0,0,0,.12)',
        }}
      ></div>
    );
  };

  render() {
    return (
      <div>
        <div
          className="topbar_background"
          id="TopBarBackground"
          style={{
            //summer: #8bf7e3
            backgroundImage: `url(${bilibili_autumn}), linear-gradient(gray,rgb(252 176 75))`,
            // backgroundColor: 'black',
            backgroundPosition: 'center center',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            width: '100%',
            height: '17vh',
            fontSize: `calc(5px + 3vmin)`,
          }}
        >
          <div style={{ width: '100%', position: 'fixed', top: '0', boxShadow: '0 0 10px #000' }}>
            <div
              className="topbar_container"
              id="topbar_container"
              style={{
                display: 'flex',
                alignItems: 'center',
                backgroundColor: `${this.state.isFirstScreen ? '' : 'black'}`,
                transition: 'background-color 0.5s',
                fontFamily: 'Lato, Helvetica Neue, Arial, Helvetica, sans-serif',
                minHeight: 50,
                height: '6vh',
                color: 'snow',
                textShadow: '0 1px 1px rgba(0, 0, 0, 0.9)',
              }}
            >
              {this.props.children}
            </div>
            {this.state.isFirstScreen ? null : this.renderTopBarBottom()}
          </div>
        </div>
        {this.renderTopBarBottom()}
      </div>
    );
  }
}

export default TopBar;
