import { observable, action } from 'mobx';
import axios from 'axios';
import { message } from '@/components/toast';
import { createBrowserHistory } from 'history';
import { cors_server } from '@/utils/const';

export interface CommonStoreInterface {
  github_username: string;
}
interface IState {}

class CommonStore<CommonStoreInterface, IState> {
  id = Math.random();
  @observable github_username = '';
  @observable github_avatar = '';
  @observable login_status = false;
  @observable whole_page_loading = false;
  @observable access_token = '';
  @observable history = createBrowserHistory();
  //只有经过验证才能访问内部页面
  @observable verification = false;

  @action
  //若已登录初始化全局状态
  init_user_info = async () => {
    this.github_username = JSON.parse(String(localStorage.getItem('github_user_info')))?.name;
    this.github_avatar = JSON.parse(String(localStorage.getItem('github_user_info')))?.avatar_url;
    this.access_token = String(localStorage.getItem('github_access_token'));
    this.login_status = true;
  };

  @action
  set_urami = async () => {
    this.verification = true;
  };

  @action
  github_login = () => {
    this.whole_page_loading = true;
    window.location.href = `https://github.com/login/oauth/authorize?client_id=1e1e4dc85cb2123d3ad0&redirect_uri=${window.location.href}&scope=public_repo`;
  };

  @action
  github_access_token = async (github_code: string) => {
    this.whole_page_loading = true;
    try {
      const res = await axios.post(
        `${cors_server}/https://github.com/login/oauth/access_token`,
        {
          code: github_code,
          client_id: '1e1e4dc85cb2123d3ad0',
          client_secret: '0452a5c2c15ed5e7d9e01f33e4a0ba75fe6e6efb',
          redirect_uri: window.location.href,
        },
        {
          headers: { accept: 'application/json' },
        }
      );
      //先存储localStorage
      //如果后存储,页面刷新时localStorage可能还没有,会触发重复请求
      localStorage.setItem('github_access_token', res.data?.access_token);
      const userInfoRes = await axios.get(`https://api.github.com/user`, {
        headers: { Authorization: `token ${res.data?.access_token}` },
      });
      localStorage.setItem('github_user_info', JSON.stringify(userInfoRes.data));
      this.whole_page_loading = false;
      //这里的replace暂时去掉。在网速差的时候有可能localstorage还没写入就跳转了。
      //之后查下怎么仅仅改变URL而不重新跳转刷新页面
      // window.location.replace(window.location.href.split('?')?.[0]);
    } catch (e) {
      message('登录失败,请稍后重试');
      setTimeout(() => {
        this.github_logout();
        window.location.replace(window.location.href.split('?')?.[0]);
      }, 3000);
    }
  };

  @action
  github_logout = () => {
    this.whole_page_loading = true;
    localStorage.clear();
    this.login_status = false;
    this.github_avatar = '';
    this.github_username = '';
    window.location.reload();
    this.whole_page_loading = false;
    window.location.replace(window.location.href.split('?')?.[0]);
  };
}
export default new CommonStore();
